import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import fromPairs from 'lodash/fromPairs';
import uniq from 'lodash/uniq';
import map from 'lodash/map';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

import Box from '../../components/Box'
import Text from '../../components/Text'
// import { mobileOrDesktop } from '../../components/ThemeProvider/theme';

import withSheetData from '../../hoc/withSheetData';
import Link from '../../components/Link';

const id = '1BzPDmwJDF_Le1gVIYGyBlB2xvB6D-LD_n-86Xd0ZKcU';

const ProjectLink = (props) => props.href ? <Link {...props} /> : <Text {...props} />

const parseRow = (data, done, y) => data.map((parsed, id) => {
  return (
    <Box my="1em" key={id}>
      {done ? (
        <Box>
          {parsed['是否結案'] && y === parsed['年份'] && (
            <ProjectLink href={parsed['歸檔資料夾']}>
              {parsed['製作物']}
            </ProjectLink>
          )}
        </Box>
      ) : (
        <Box>
          {!parsed['是否結案'] && (
            <ProjectLink href={parsed['歸檔資料夾']}>
              {parsed['製作物']}
            </ProjectLink>
          )}
        </Box>
      )}
    </Box>
  )
})

class Project extends PureComponent {
  constructor(props) {
    super(props);
    const clonedData = Array.from(props.data);
    const header = clonedData.shift();
    const yearIndex = header.findIndex((o) => o === '年份');
    const allYears = map(clonedData, yearIndex)
    this.years = uniq(allYears).filter(y => !isNaN(y)).map((label) => label).sort((a, b) => { return  b - a });
    this.data = clonedData.map((row) => fromPairs(row.map((value, index) => [header[index], value])));
  }

  render() {
    const { done, search, ...props } = this.props;
    // const filteredData = this.data
    const filteredData = this.data.filter((parsed) => parsed['製作物'] && parsed['製作物'].toLowerCase().includes(search.toLowerCase()));
    return (
      <Box {...props}>
        {done ? (
          <Box py="2em">
            <Tabs>
              <TabList>
                {this.years.map((year, index) => (
                  <Tab key={index}>{year}</Tab>
                ))}
              </TabList>
              {this.years.map((year, index) => (
                <TabPanel key={index}>
                  <Box>{parseRow(filteredData, done, year)}</Box>
                </TabPanel>
              ))}
            </Tabs>
          </Box>
        ) : (
          <Box my="2em">{parseRow(filteredData, done)}</Box>
        )}
      </Box>
    )
  }
}


Project.propTypes = {
  range: PropTypes.string,
};

export default withSheetData({ id, range: '歸檔表單!A2:G' })(Project);
