import React, { PureComponent } from 'react';
import { GoogleSheetsApi } from '../../vendor/react-google-sheet'

import Blob from './Blob';
import Project from './Project';
import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Button from '../../components/Button';
import InputWithClear from '../../components/InputWithClear';

const CLIENT_ID = '871831599839-67kmk9oreergrthq16b64qrdb41b4jcv.apps.googleusercontent.com';
const API_KEY = 'AIzaSyA3SEKynUtt2EO2r_LnBTtlL4xkmNu6H-E';

class App extends PureComponent {
  state = {
    done: false,
    search: '',
  }

  projected = () => {
    this.setState({ done: true })
  }

  projecting = () => {
    this.setState({ done: false })
  }

  handleSearch = (e) => {
    this.setState({ search: e.target.value });
  }

  // handleChange = (value) => {
  //   this.setState({ year: value })
  // }

  render() {
    const { done, search } = this.state;
    return (
      <GoogleSheetsApi clientId={CLIENT_ID} apiKey={API_KEY}>
        {({ authorize, loading: apiLoading, signout, signedIn, error }) => (
          <Box>
            {apiLoading ? (
              <div>Loading...</div>
            ) : error ? (
              <Blob data={error} />
            ) : signedIn ? (
              <Box p="1em">
                <Button onClick={signout}>登出</Button>
              </Box>
            ) : (
              <Box p="1em">
                <Button onClick={authorize}>登入</Button>
              </Box>
            )}
            {signedIn && (
              <Box px="2em">
                <Flex my="1.5em" mx="-0.5em">
                  <Box mx="0.5em">
                    <Button active={!done} onClick={this.projecting}>未結案</Button>
                  </Box>
                  <Box mx="0.5em">
                    <Button active={done} onClick={this.projected}>已結案</Button>
                  </Box>
                </Flex>
                <Box>
                  <InputWithClear onChange={this.handleSearch} placeholder="輸入關鍵字搜尋" />
                </Box>
                <Project done={done} search={search} />
              </Box>
            )}
          </Box>
        )}
      </GoogleSheetsApi>
    );
  }
}

export default App;
